*{
  box-sizing: border-box;
  padding: 0;
  margin : 0;
  font-family: Arial, Helvetica, sans-serif;
}

.main_div{
  width: 100%;
  height: 100vh;
  background:#6983aa;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center_div{
 width: 25%;
 height: 70vh;
 background-color: #f4f4f4;
 box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
 border-radius: 15px;
}

h1{
  color: grba(0,0,0,0.87);
  font-size: 50px;
  margin-bottom: 30px;
  min-width: 64px;
  box-sizing: border-box;
  font-weight: 500;
  border-radius: 4px;
  letter-spacing: 0.02857em;
  text-transform: uppercase;
  text-shadow: 5px 5px 5px rgba(0,0,0,0.3);

}

input{
  text-align: center;
  height: 30px;
  border: none;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
  margin-right: 20px;

}

.newBtn {
  min-height: 40px;
  width: 40px;
  background-color: #16a085 !important;
  color: white !important;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
  border-radius: 50% !important;
  font-size: 40px;
}

.newBtn:hover{
  color: #16a085 !important;
  background: white !important;
}

.MuiSvgIcon-root{
  width: 2rem !important;
  height: 2rem !important;
}

ol{
  margin-top: 30px;
}

ol li{
  padding: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
  list-style: none;
}

.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .deleteIcon{
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  padding: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}

.todo_style .deleteIcon:hover{
  background-color: transparent;
  color: crimson;
  box-shadow: 5px 5px 15px -5px rgba(255, 0, 0, 0.5);
}

.todo_style .fa-times:hover .list_style{
  color: crimson;
}

@media (max-width: 768px){
  .center_div{
    width: 55%;
  }

  .todo_style .fa-times{
    margin: 0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .newBtn{
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: #ffffff;
    font-size: 30px;
  }
}